* {
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.App-intro {
  margin: 10% 10%;
}

.App-smallContent {
  font-size: 1rem;
  color: pink;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.App-block {
  text-align: left;
  margin: 10% 10%;
  padding: 30px;
  max-width: 900px;
  width: 80%;
  font-size: 1rem;
  border: 1px dotted white;
  line-height: 1.5rem;
}

/* App Project*/

.App-gallery {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.App-project {
  color: black;
  width: 25%;
  height: 200px;
  margin: 10px;
  padding: 10px;
  border-radius: 1px;
  box-shadow: 2px 2px 2px 1px rgba(255, 255, 255, .2);
  background-color: pink;
}


